*{
    margin:0;
    padding:0;
    text-align: center;
    font-size: 1.1rem;
}
#root{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: rgb(0, 0, 0);
}
#break-label, #session-label,#timer, #controls, h1{
    width: 80%;
    display: flex;
    justify-content: space-around;
    align-items: center;
    flex-wrap: wrap;
}
#break-label p, #session-label p{
    width: 150px;
}
#timer{
    flex-direction: column;
    border:1px white solid;
    border-radius: 20px;
    width: 30%;
    gap: 10px;
    padding: 5%;
    background-color: rgb(0, 0, 0);
}
.incDec{
    display: flex;
    width: 100px;
    justify-content: space-between;
}
button{
    width: 30px;
    height: 30px;
    background: none;
    border: none;
    cursor: pointer;
}

button:hover i{
    color: white;
}

#start_stop{
    width: 50px;
}
h1{
    font-size: larger;
}
.App{
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    padding: 20px;
    background-color: rgb(0, 0, 0);
    border:1px white solid;
    width: 30%;
    height: 50%;
}
@media screen and (max-width:700px) {
    .App{
        width: 80%;
    }
    
}
